export default {
	data() {
		return {
			isCard: true,
			sayList: null,
			index: '',
			isthumbColor: "#ff5500",
			discussList: [],
			discuss: ""
		}
	},
	onLoad(e) {
		this.sayList = uni.getStorageSync('recommendList')
		this.index = e.index
		console.log(this.sayList[e.index].picUrl)
		
		uni.request({
		    url: this.Server_IP + 'queryDiscuss', //仅为示例，并非真实接口地址。
		    data: {
				textId: this.sayList[this.index].textId
		    },
		    header: {
		        'custom-header': 'queryDiscuss' //自定义请求头信息
		    },
			method:"POST",
			dataType:"json",
		    success: (res) => {
		        console.log(res.data);
				if(res.data.info.code == '0'){
					this.discussList = JSON.parse(res.data.data.discuss)
				}else{
					uni.showToast({
						icon: 'none',
						title: "获取评论失败"
					});
				}
		    },
		});
	},
	methods: {
		ipreviewImage(idx, picUrl) {
			console.log("11")
			console.log(idx)
			console.log(picUrl)
			let preview = picUrl;
			uni.previewImage({
				current: idx,
				urls: preview
			});
		},
		getTimeFormat(timeStamp){
			var time = ""
			var date = new Date(parseInt(timeStamp))
			time += date.getFullYear() + "年"
			time += date.getMonth()+1 + "月"
			time += date.getDate() + "日 "
			time += date.getHours()+ ":"
			time += date.getMinutes() + ":"
			time += date.getSeconds()
			return time
		},
		
		todiscuss(){
			uni.showToast({
				icon: 'none',
				title: "请先登录"
			});
		},
		Thumb(){
			uni.showToast({
				icon: 'none',
				title: "请先登录"
			});
		}
	}
}
